main {
  
}

.maze-grid {
  background-color: #DFDBE5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

.maze {
  border: 3px solid #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.controls {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px 0;
}

.maze-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 10px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s
}

.maze-button:hover {
  background-color: #0056b3;
  transition: 0.3s;
}

.maze-button:disabled {
  outline: 2px solid #007bff;
  background-color: #cccccc;
  color: #888888;
  cursor: not-allowed;
}

.row {
  display: flex;
  flex-direction: row;
}

.cell {
  width: 30px;
  height: 30px;
  border: 1px solid #666;
}

.cell.visited {
  background-color: lightblue;
}

.cell.wall {
  background-color: black;
}

.cell.path {
  background-color: #DFDBE5;
}

.cell.start {
  background-color: green;
}

.cell.end {
  background-color: red;
}

.cell.path-found {
  background-color: lightgreen;
}

@media screen and (max-width: 800px) {
  .maze-button {
    font-size: medium;
    width: 90%;
    margin: 5px;
  }
  
  .cell {
    width: 24px;
    height: 24px;
  }
}

@media screen and (max-width: 600px) {
  .maze-button {
    font-size: small;
    width: 80%;
    margin: 5px;
  }
  
  .cell {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .maze-button {
    width: 100%;
    padding: 5px;
  }
  
  .cell {
    width: 16px;
    height: 16px;
  }
}